body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*:focus {
    outline: none;
}

* {
    color: rgb(78,78,78);
}

#logo {
    width: 65vw;
    margin: 10px 15vw 20px 1vw;
}

@media(min-width: 265px){
    #logo {
        width: 70vw;
    }
}

@media(min-width: 320px){
    #logo {
        width: 75vw;
    }
}

@media(min-width: 400px){
    #logo {
        width: 80vw;
    }
}

@media(min-width: 780px){
    #logo {
        width: 85vw;
        margin-left: 3vw;
    }
}

@media(min-width: 1000px){
    #logo {
        width: 90vw;
    }
}

@media(min-width: 1400px){
    #logo {
        width: 95vw;
    }
}
@media(min-width: 750px) {
  #products-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}
sup {
    font-size: 70%;
}

.product-tile-container {
    border: 1px solid rgb(223, 223, 223);
    border-radius: 10px;
    margin: 10px auto;
    text-align: center;
    padding: 5px;
    color: rgb(78, 78, 78);
    max-width: 90%;
}

.product-tile-image {
    max-height: 70vh;
    max-width: 80%;
    margin: 5px auto;
}

.size-selector {
    list-style: none;
    padding: 0;
}

.size-selector * {
    display: inline-block;
    margin: 5px 10px;
    padding: 5px;
    border: 1px solid rgb(78, 78, 78);
    border-radius: 15px;
}

.size-selector *:hover {
    cursor: pointer;
}

.selected {
    background-color: rgb(78, 78, 78);
    color: white;
}

.add-to-cart-container {
    margin: 10px auto;
    padding: 10px 0;
}

.add-to-cart-quantity {
    font-size: 15px;
    width: 50px;
    padding: 5px 10px 5px 15px;
    border: 1px solid rgb(78, 78, 78);
    border-radius: 15px;
}

.add-to-cart-button {
    display: inline;
    padding: 5px 20px;
    border: 1px solid rgb(78, 78, 78);
    border-radius: 15px;
}

.add-to-cart-button:hover {
    cursor: pointer;
}

.add-to-cart-button:active {
    background-color: rgb(78, 78, 78);
    color: white;
}

.limited-edition-label {
    background-color: red;
    color: white;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
    padding: 0 15px;
    border-radius: 30px;
    font-size: 20px;
}

#sold-out-label {
    color: red;
    font-size: 2.2em;
}

@media(min-width: 820px){
    .limited-edition-label {
        font-size: 25px;
    }
}

@media(min-width: 1200px){
    .limited-edition-label {
        font-size: 30px;
    }
}

#cart-container {
    width: 100px;
    height: 100px;
    background-color: rgb(78,78,78);
    border-radius: 50%;
    position: fixed;
    top: -40px;
    right: -35px;
    z-index: 2;
}

#cart-container:hover {
    cursor: pointer;
}

#cart-content {
    position: relative;
    top: 45%;
    left: 10%;
}

#cart-content * {
    display: inline;
}

#cart-x {
    color: white;
    margin-left: 15px;
}

#cart-image {
    width: 40px;
}

#cart-count {
    background-color: white;
    color: rgb(78,78,78);
    font-size: 80%;
    font-weight: 800;
    padding: 1px 5px 3px;
    border-radius: 100px;
    position: relative;
    right: 10px;
    bottom: 30px;
}
#cart {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    background-color: rgba(255, 255, 255, 0.925);
    overflow-y: scroll;
    -webkit-animation: slide-up .8s;
            animation: slide-up .8s;
}

@-webkit-keyframes slide-up{
    from{
        position: fixed;
        top: 110vh;
    }
    to{
        position: fixed;
        top: 0;
    }
}

@keyframes slide-up{
    from{
        position: fixed;
        top: 110vh;
    }
    to{
        position: fixed;
        top: 0;
    }
}

#payment-confirmation-container {
    width: 90vw;
    height: 90vh;
    background-color: rgba(231, 231, 231, 0.9);
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 3;
}

#payment-confirmation-statement {
    width: 95%;
    position: relative;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -100%);
            transform: translate(-50%, -100%);
    text-align: center;
}

#cart-summary {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    background-color:white;
    border-top: 1px solid rgb(78, 78, 78);
    border-radius: 30px;
}

#cart-summary-content {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto 30px;
}

#cart-summary-total {
    text-align: center;
}

.cart-item {
    width: 90vw;
    margin: 10px auto 50px;
    padding: 0 10px 70px;
    text-align: center;
    border-bottom: 1px solid rgb(223, 223, 223);
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.last {
    margin-bottom: 25vh;
}

.cart-item-image {
    width: 50%;
    margin: 0 5vw 0 auto;
}

.cart-item-title {
    font-size: 100%;
    margin: 25% auto 0;
}

.cart-item-qty-container {
    margin: 25% auto 0;
}

.update-cart-quantity {
    font-size: 15px;
    width: 35px;
    padding: 5px 10px 5px 15px;
    border: 1px solid rgb(78, 78, 78);
    border-radius: 15px;
}

.update-cart-button {
    margin-top: 5px;
    padding: 5px 20px;
    border: 1px solid rgb(78, 78, 78);
    border-radius: 15px;
}

.update-cart-button:hover {
    cursor: pointer;
}

.update-cart-button:active {
    background-color: rgb(78, 78, 78);
    color: white;
}

#shipping {
    font-size: 50%;
}

#intl-shipping-button {
    width: 250px;
    height: 40px;
    color: white;
    background: -webkit-gradient(linear, left top, left bottom, from(rgb(0, 0, 255)), to(rgb(0, 0, 161)));
    background: linear-gradient(to bottom, rgb(0, 0, 255), rgb(0, 0, 161));
    margin: 0 auto 20px;
    font-weight: 800;
    border: 0;
    border-radius: 10px;
}

#intl-shipping-button:hover {
    cursor: pointer;
}

#discount-code-container {
    height: 40px;
    margin: 0 auto 20px;
}

#discount-code-input {
    font-size: 22px;
    width: 200px;
}

#discount-code-button {
    height: 40px;
    padding: 0 10px;
    color: white;
    background: -webkit-gradient(linear, left top, left bottom, from(rgb(0, 0, 255)), to(rgb(0, 0, 161)));
    background: linear-gradient(to bottom, rgb(0, 0, 255), rgb(0, 0, 161));
    font-weight: 800;
    border: 0;
    border-radius: 10px;
    margin-left: 5px;
}

#discount-code-button:hover {
    cursor: pointer;
}

@media(min-width: 900px){
    .update-cart-button {
        display: inline;
    }
    .cart-item-image {
        margin-top: 0;
    }
    .cart-item-title {
        font-size: 20px;
    }
    #cart-summary-content {
        display: grid;
        grid-template-columns: 3fr 3fr 3fr;
        margin-bottom: 10px;
    }
    #cart-summary-paypal {
        padding: 10% 0;
    }
    #intl-shipping-button {
       margin: 25px 30px 20px 20px;
    }
}
#footer-container {
    border-top: 1px solid rgb(223, 223, 223);
    padding-bottom: 30px;
    text-align: center;
    border-radius: 30px;
}

.footer-section {
    text-align: center;
    margin-bottom: 40px;
}

.social-icon {
    position: relative;
    z-index: 0;
    margin: 0 10px;
    -webkit-transition-duration: .3s;
            transition-duration: .3s;
}

.social-icon:hover {
    -webkit-transform: scale(1.2) rotate(360deg);
            transform: scale(1.2) rotate(360deg);
}

#email {
    font-size: 1.3em;
}

@media(min-width: 750px){
    .footer-section{ 
        display: inline-block;
        width: 30vw;
    }
}
